import { useState, useEffect } from "react";
import { Logo } from "../../components/logo/Logo";
import { Spinner } from "../../components/spinner/Spinner";
import { Error } from "../../components/error/Error";
import { DailySheet } from "../../components/sheets/Sheets";
import { BASE_URL, URLS } from "../url";
import { Link } from "react-router-dom";

const Home = () => {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  const URL = BASE_URL + URLS.today;

  useEffect(() => {
    fetch(URL)
      .then((response) => {
        if (response.ok) return response.json();
        throw response;
      })
      .then((data) => {
        setData(JSON.parse(data.todayhisab));
      })
      .catch((error) => {
        console.error("Error Fetching data: ", error);
        setError(error);
      })
      .finally(() => {
        setLoading(false);
      });
  }, []);

  return (
    <div>
      <Logo />
      {loading == true ? (
        <Spinner />
      ) : (
        <div className="mt-5 pt-5">
          {error ? <Error /> : <DailySheet items={data} />}
        </div>
      )}
      <div className="my-5 py-4 flex justify-center">
        <button className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded">
          <Link to="add">Add Entry</Link>
        </button>
      </div>
    </div>
  );
};

export default Home;
