import "./assets/css/index.css";
import Home from "./pages/home/Home";
import Navbar from "./components/navbar/NavBar";
import {
  DailyAppend,
  ProductAppend,
  CustomerAppend,
} from "./components/forms/Form";

import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

function App() {
  return (
    <div>
      <Router basename={window.location.pathname || ""}>
        <div className="App">
          <Navbar />
          <Switch>
            <Route exact path="/" component={Home}></Route>
            <Route exact path="/dailyappend" component={DailyAppend}></Route>
            <Route
              exact
              path="/productappend"
              component={ProductAppend}
            ></Route>
            <Route
              exact
              path="/customerappend"
              component={CustomerAppend}
            ></Route>
          </Switch>
        </div>
      </Router>
    </div>
  );
}

export default App;
