import { useState } from "react";
import spinner from "../Spinner-1s-200px.svg";
import { BASE_URL, URLS } from "../../pages/url";
import { Error } from "../../components/error/Error";

const CustomerSheet = () => {
  const initalState = {
    date: "",
    name: "",
    number: "",
    alt_number: "",
    add1: "",
    add2: "",
    city: "",
    bill: "",
  };
  const [formData, setFormData] = useState({
    date: "",
    name: "",
    number: "",
    alt_number: "",
    add1: "",
    add2: "",
    city: "",
    bill: "",
  });
  const [error, setError] = useState(null);
  const [loading, setLoading] = useState(false);
  const [formErrorMsg, setFormErrorMsg] = useState("");

  const validate = () => {
    if (formData["date"] === "") {
      setFormErrorMsg("Date can not be empty");
      return false;
    } else if (formData["sold"] < 0 || formData["purchase"] < 0) {
      setFormErrorMsg("Rupess can not be negative");
      return false;
    } else if (formData["quantity"] < 1) {
      setFormErrorMsg("Quantity should atleast be 1");
      return false;
    }
    return true;
  };

  const handleOnChange = (e) => {
    const id = e.target.id;
    const value = e.target.value;
    setFormData({
      ...formData,
      [id]: value,
    });
  };

  const handleOnSubmit = (e) => {
    e.preventDefault();
    if (validate()) {
      setLoading(true);
      const data = new FormData();

      let sheet_name = "Customer Detail";

      data.append("date", formData["date"]);
      data.append("name", formData["name"]);
      data.append("number", formData["number"]);
      data.append("alt_number", formData["alt_number"]);
      data.append("add1", formData["add1"]);
      data.append("add2", formData["add2"]);
      data.append("city", formData["city"]);
      data.append("bill", formData["bill"]);
      data.append("sheet_name", sheet_name);

      const URL = BASE_URL + URLS.add;

      fetch(URL, {
        method: "post",
        body: data,
      })
        .then((response) => {
          if (response.ok) return response.json();
          else {
            throw new Error("Error adding Entery to Customer Sheet");
          }
        })
        .then((data) => {
          if (data.msg == "SUCSESS") alert(data.msg);
          else setError(data.msg);
        })
        .catch((error) => {
          console.error("Error ", error);
          setError(error);
        })
        .finally(() => {
          setLoading(false);
        });

      resetForm();
    }
  };

  const resetForm = () => {
    document.getElementById("customer-sheet").reset();
    //clearing state
    setFormData({ ...initalState });
  };

  return (
    <div>
      {error ? (
        <Error />
      ) : (
        <div className="m-5 p-3">
          <form
            className="w-full max-w-lg"
            id="customer-sheet"
            onSubmit={handleOnSubmit}
          >
            <div className="flex flex-wrap -mx-3 mb-6">
              <div className="w-full md:w-1/2 px-3 mb-6 md:mb-0">
                <label className="block uppercase tracking-wide text-gray-700 text-xl font-bold mb-2">
                  Date
                </label>
                <input
                  className="appearance-none block w-full bg-gray-200 text-gray-700 border border-blue-500 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white"
                  onInput={handleOnChange}
                  type="date"
                  id="date"
                />
              </div>
              <div className="w-full md:w-1/2 px-3 mb-6 md:mb-0">
                <label className="block uppercase tracking-wide text-gray-700 text-xl font-bold mb-2">
                  Full Name
                </label>
                <input
                  className="appearance-none block w-full bg-gray-200 text-gray-700 border border-blue-500 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white"
                  id="name"
                  onInput={handleOnChange}
                  type="text"
                  placeholder="Good Name"
                />
              </div>
              <div className="w-full md:w-1/2 px-3 mb-6 md:mb-0">
                <label className="block uppercase tracking-wide text-gray-700 text-xl font-bold mb-2">
                  Mobile Number
                </label>
                <input
                  className="appearance-none block w-full bg-gray-200 text-gray-700 border border-blue-500 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white"
                  onInput={handleOnChange}
                  type="number"
                  id="number"
                  placeholder="Number"
                />
              </div>
              <div className="w-full md:w-1/2 px-3 mb-6 md:mb-0">
                <label className="block uppercase tracking-wide text-gray-700 text-xl font-bold mb-2">
                  Alternate Number
                </label>
                <input
                  className="appearance-none block w-full bg-gray-200 text-gray-700 border border-blue-500 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white"
                  onInput={handleOnChange}
                  type="number"
                  id="alt_number"
                  placeholder="Number"
                />
              </div>
              <div className="w-full md:w-1/2 px-3 mb-6 md:mb-0">
                <label className="block uppercase tracking-wide text-gray-700 text-xl font-bold mb-2">
                  Gali/Block/House
                </label>
                <input
                  className="appearance-none block w-full bg-gray-200 text-gray-700 border border-blue-500 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white"
                  id="add1"
                  onInput={handleOnChange}
                  type="text"
                  placeholder="Address"
                />
              </div>
              <div className="w-full md:w-1/2 px-3 mb-6 md:mb-0">
                <label className="block uppercase tracking-wide text-gray-700 text-xl font-bold mb-2">
                  Mohalla
                </label>
                <input
                  className="appearance-none block w-full bg-gray-200 text-gray-700 border border-blue-500 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white"
                  id="add2"
                  onInput={handleOnChange}
                  type="text"
                  placeholder="Address 2"
                />
              </div>
              <div className="w-full md:w-1/2 px-3 mb-6 md:mb-0">
                <label className="block uppercase tracking-wide text-gray-700 text-xl font-bold mb-2">
                  City
                </label>
                <input
                  className="appearance-none block w-full bg-gray-200 text-gray-700 border border-blue-500 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white"
                  id="city"
                  onInput={handleOnChange}
                  type="text"
                  placeholder="For eg: Ratlam"
                />
              </div>
              <div className="w-full md:w-1/2 px-3 mb-6 md:mb-0">
                <label className="block uppercase tracking-wide text-gray-700 text-xl font-bold mb-2">
                  Bill No.
                </label>
                <input
                  className="appearance-none block w-full bg-gray-200 text-gray-700 border border-blue-500 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white"
                  onInput={handleOnChange}
                  id="bill"
                  type="number"
                  placeholder="Bill No"
                />
              </div>
              <div>
                {formErrorMsg !== "" ? (
                  <div className="w-full md:w-1/2 px-3 mb-6 md:mb-0">
                    <div
                      class="text-red-700 px-4  rounded relative"
                      role="alert"
                    >
                      <p class="text-sm leading-tight">*{formErrorMsg}*</p>
                    </div>
                  </div>
                ) : (
                  <div></div>
                )}
                <div className="md:w-2/3">
                  {loading ? (
                    <div className="mx-5  flex">
                      <img src={spinner} className="w-10" alt="Loading" />
                      <button
                        className=" shadow focus-shadow-outline focus-outline-none text-white font-bold px-5 rounded bg-blue-500 hover:bg-blue-400"
                        disable
                      >
                        {" "}
                        processing
                      </button>
                    </div>
                  ) : (
                    <button
                      className="shadow bg-blue-500 hover:bg-blue-400 focus:shadow-outline focus:outline-none text-white font-bold py-2 mx-5 px-4 rounded"
                      type="submit"
                    >
                      Add Entery
                    </button>
                  )}
                </div>
              </div>
            </div>
          </form>
        </div>
      )}
    </div>
  );
};

export default CustomerSheet;
