export function DailySheet({ items }) {
  let credit = 0;
  let debit = 0;
  for (var i = 0; i < items.length; ++i) {
    credit += Number(items[i]["Cash In"]);
    debit += Number(items[i]["Cash Out"]);
  }
  return (
    <div>
      <div className="flex flex-col h-screen">
        <div className="flex-grow overflow-auto">
          <table className="min-w-max w-full table-auto">
            <thead>
              <tr className="bg-gray-200 text-black-900 uppercase text-xl leading-normal">
                <th className="sticky top-0 px-6 py-3 ">Credit</th>
                <th className="sticky top-0 px-6 py-3 ">Debit</th>
                <th className="sticky top-0 px-6 py-3 ">Pending</th>
                <th className="sticky top-0 px-6 py-3 ">Category</th>
                <th className="sticky top-0 px-6 py-3 ">Description</th>
                <th className="sticky top-0 px-6 py-3 ">Date</th>
                <th className="sticky top-0 px-6 py-3 ">Bill No</th>
              </tr>
            </thead>
            <tbody className="divide-y">
              {items.map((item, index) => (
                <tr
                  key={index}
                  className={
                    item["Bill No."] == ""
                      ? "bg-red-100"
                      : "bg-green-100" + " text-lg"
                  }
                >
                  <td className="px-6 py-4 text-center">{item["Cash In"]}</td>
                  <td className="px-6 py-4 text-center">{item["Cash Out"]}</td>
                  <td className="px-6 py-4 text-center">{item["Pending"]}</td>
                  <td className="px-6 py-4 text-center">{item["Category"]}</td>
                  <td className="px-6 py-4 text-center">
                    {item["Description"]}
                  </td>
                  <td className="px-6 py-4 text-center">
                    {new Date(item["Date"]).getDate()}
                  </td>
                  <td className="px-6 py-4 text-center">{item["Bill No."]}</td>
                </tr>
              ))}

              <tr className={"bg-blue-400 text-lg m-5]"}>
                <td className="px-6 py-4 text-center">{credit}</td>
                <td className="px-6 py-4 text-center">{debit}</td>
                <td className="px-6 py-4 text-center">Total </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
}
