import { useState } from "react";
import { BASE_URL, URLS } from "../../pages/url";
import { Error } from "../../components/error/Error";
import { DailyCategory } from "./data";
import spinner from "../Spinner-1s-200px.svg";

const DailySheet = () => {
  const initalState = {
    date: "",
    cashIn: "",
    cashOut: "",
    pending: "",
    bill: "",
    category: "",
    desc: "",
  };
  const [formData, setFormData] = useState({
    date: "",
    cashIn: "",
    cashOut: "",
    pending: "",
    bill: "",
    category: "",
    desc: "",
  });

  const [error, setError] = useState(null);
  const [formErrorMsg, setFormErrorMsg] = useState("");
  const [loading, setLoading] = useState(false);

  const handleOnChange = (e) => {
    const id = e.target.id;
    const value = e.target.value;
    setFormData({
      ...formData,
      [id]: value,
    });
  };

  const validate = () => {
    if (formData["date"] === "") {
      setFormErrorMsg("Date can not be empty");
      return false;
    } else if (
      formData["pending"] < 0 ||
      formData["cashIn"] < 0 ||
      formData["cashOut"] < 0
    ) {
      setFormErrorMsg("Rupess can not be negative");
      return false;
    }
    return true;
  };

  const handleOnSubmit = (e) => {
    e.preventDefault();
    setFormErrorMsg("");
    if (validate()) {
      setLoading(true);
      const data = new FormData();
      let sheet_name = "Royal Daily";

      data.append("date", formData["date"]);
      data.append("cashIn", formData["cashIn"]);
      data.append("cashOut", formData["cashOut"]);
      data.append("pending", formData["pending"]);
      data.append("bill", formData["bill"]);
      data.append("category", formData["category"]);
      data.append("desc", formData["desc"]);
      data.append("sheet_name", sheet_name);

      const URL = BASE_URL + URLS.add;

      fetch(URL, {
        method: "post",
        body: data,
      })
        .then((response) => {
          if (response.ok) return response.json();
          else {
            throw new Error("Error adding Entery to Royal Daily");
          }
        })
        .then((data) => {
          if (data.msg == "SUCSESS") alert(data.msg);
          else setError(data.msg);
        })
        .catch((error) => {
          console.error("Error ", error);
          setError(error);
        })
        .finally(() => {
          setLoading(false);
        });
      resetForm();
    }
  };

  const resetForm = () => {
    document.getElementById("royal-daily").reset();
    //clearing state
    setFormData({ ...initalState });
  };

  return (
    <div>
      {error ? (
        <Error />
      ) : (
        <div className="m-5 p-3">
          <form
            className="w-full max-w-lg"
            id="royal-daily"
            onSubmit={handleOnSubmit}
          >
            <div className="flex flex-wrap -mx-3 mb-6">
              <div className="w-full md:w-1/2 px-3 mb-6 md:mb-0">
                <label className="block uppercase tracking-wide text-gray-700 text-xl font-bold mb-2">
                  Date
                </label>
                <input
                  className="appearance-none block w-full bg-gray-200 text-gray-700 border border-blue-500 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white"
                  onInput={handleOnChange}
                  type="date"
                  id="date"
                />
              </div>

              <div className="w-full md:w-1/2 px-3 mb-6 md:mb-0">
                <label className="block uppercase tracking-wide text-gray-700 text-xl font-bold mb-2">
                  CashIn
                </label>
                <input
                  className="appearance-none block w-full bg-gray-200 text-gray-700 border border-blue-500 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white"
                  id="cashIn"
                  onInput={handleOnChange}
                  type="number"
                  placeholder="Amount"
                />
              </div>

              <div className="w-full md:w-1/2 px-3 mb-6 md:mb-0">
                <label className="block uppercase tracking-wide text-gray-700 text-xl font-bold mb-2">
                  CashOut
                </label>
                <input
                  className="appearance-none block w-full bg-gray-200 text-gray-700 border border-blue-500 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white"
                  onInput={handleOnChange}
                  type="number"
                  id="cashOut"
                  placeholder="Amount"
                />
              </div>

              <div className="w-full md:w-1/2 px-3 mb-6 md:mb-0">
                <label className="block uppercase tracking-wide text-gray-700 text-xl font-bold mb-2">
                  Pending
                </label>
                <input
                  className="appearance-none block w-full bg-gray-200 text-gray-700 border border-blue-500 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white"
                  id="pending"
                  type="number"
                  onInput={handleOnChange}
                  placeholder="Amount"
                />
              </div>

              <div className="w-full md:w-1/2 px-3 mb-6 md:mb-0">
                <label className="block uppercase tracking-wide text-gray-700 text-xl font-bold mb-2">
                  Bill No.
                </label>
                <input
                  className="appearance-none block w-full bg-gray-200 text-gray-700 border border-blue-500 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white"
                  onInput={handleOnChange}
                  id="bill"
                  type="number"
                  placeholder="Bill No"
                />
              </div>

              <div className="w-full md:w-1/3 px-3 mb-6 md:mb-0">
                <label className="block uppercase tracking-wide text-gray-700 text-xl font-bold mb-2">
                  Category
                </label>
                <div className="relative">
                  <select
                    className="block appearance-none w-full bg-gray-200 border border-blue-500 text-gray-700 py-3 px-4 pr-8 rounded leading-tight focus:outline-none focus:bg-white focus:border-gray-500"
                    onChange={handleOnChange}
                    id="category"
                  >
                    {DailyCategory.map((item) => (
                      <option> {item} </option>
                    ))}
                  </select>
                  <div className="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700">
                    <svg
                      className="fill-current h-4 w-4"
                      xmlns="http://www.w3.org/2000/svg"
                      viewBox="0 0 20 20"
                    >
                      <path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z" />
                    </svg>
                  </div>
                </div>
              </div>

              <div className="w-full md:w-1/2 px-3 mb-6 md:mb-0">
                <label className="block uppercase tracking-wide text-gray-700 text-xl font-bold mb-2">
                  Description
                </label>
                <input
                  className="appearance-none block w-full bg-gray-200 text-gray-700 border border-blue-500 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white"
                  id="desc"
                  onChange={handleOnChange}
                  type="text"
                  placeholder="Detail"
                />
              </div>
              <div>
                {formErrorMsg !== "" ? (
                  <div className="w-full md:w-1/2 px-3 mb-6 md:mb-0">
                    <div
                      class="text-red-700 px-4  rounded relative"
                      role="alert"
                    >
                      <p class="text-sm leading-tight">*{formErrorMsg}*</p>
                    </div>
                  </div>
                ) : (
                  <div></div>
                )}
                <div className="md:w-2/3">
                  {loading ? (
                    <div className="mx-5  flex">
                      <img src={spinner} className="w-10" alt="Loading" />
                      <button
                        className=" shadow focus-shadow-outline focus-outline-none text-white font-bold px-5 rounded bg-blue-500 hover:bg-blue-400"
                        disable
                      >
                        {" "}
                        processing
                      </button>
                    </div>
                  ) : (
                    <button
                      className="shadow bg-blue-500 hover:bg-blue-400 focus:shadow-outline focus:outline-none text-white font-bold py-2 mx-5 px-4 rounded"
                      type="submit"
                    >
                      Add Entery
                    </button>
                  )}
                </div>
              </div>
            </div>
          </form>
        </div>
      )}
    </div>
  );
};

export default DailySheet;
